body {
  font-family:New Cowboy Demo;
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 80vh;

  background: url(Images/grocery1.jpg);
  background-repeat: no-repeat;
  background-size: cover;
}

.todo-app 
{
  width: 500px;
  margin-top:3px; 
  color:rgb(8, 85, 6);
  
}

h1 {
  font-size:45px;
  text-align: center;
  margin-bottom: 5px;
  color:rgb(5, 64, 122);
  
}

h2{
  font-size: 25px;
  text-align: center;
  text-decoration: dotted;
  
}

input {
  width: calc(100% - 30px);
  padding: 20px 10px;
  margin-bottom: 10px;
  font-size: 16px;
  border-radius: 30px;
  border: 2px solid black ;
  font-family: Monotype Corsiva;
  font-weight: bold;
}

button {
  width: 30%;
  background-color:rgb(66, 63, 63);
  color: bisque;
  border:none;
  outline:none;
  cursor: pointer;
  font-size: 16px;
  margin-top: 10px;
  margin-left: 140px;
  padding: 5px 10px;
  justify-content: center;
  align-items: center;
  border-radius: 30px;
  border: 2px solid black ;
  font-family: Monotype Corsiva;
  font-weight: bold;
  font-size: 20px;
}

button:hover {
  background-color:rgb(57, 178, 218);
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: flex;
  justify-content: space-between;
  padding: 10px;
  background-color: #f9f9f9;
  margin-bottom: 10px;
  border-radius: 4px;
}

li button {
  background-color:rgb(66, 63, 63);
  margin-left:80px;
  font-size: 14px;
  padding: 3px 8px;
  cursor: pointer;
  border-radius: 30px;
  border: 2px solid black ;
  font-family: Monotype Corsiva;
  font-weight: bold;
  font-size: 20px;
  width: 40%;
}

li button:hover {
  background-color:rgb(57, 178, 218);
}